import { combineReducers } from "redux";
import { todosReducer } from "./todos";
import { authReducer } from "./auth";
import { proctoringReducer } from "./proctoring";
import { systemReducer } from "./system";

import { Todo, Auth, Socket, Proctoring, examDataInterface, System, applicationPeriodsStoreInterface } from "../actions";
import { socketReducer } from "./socket";

import { doExamReducer } from "./exam";
import { applicationStoreInterface } from "../actions/application";
import { applicationReducer } from "./application";
import { applicationsListStoreInterface } from "../actions/ApplicationsList";
import { applicationsListReducer } from "./applications_list";
import { positionsStoreInterface } from "../actions/positions";
import { positionsReducer } from "./positions";
import { applicationPeriodsReducer } from "./application_periods";
import { schoolLevelsStoreInterface } from "../actions/school_levels";
import { schoolLevelsReducer } from "./school_levels";

// define the entire state into the entire sire
export interface StoreState {
  todos: Todo[];
  auth: Auth;
  socket: Socket;
  doExam: examDataInterface;
  application: applicationStoreInterface;
  proctoring: Proctoring;
  system: System;
  applications_list: applicationsListStoreInterface;
  positions: positionsStoreInterface;
  periods: applicationPeriodsStoreInterface;
  schoolLevels: schoolLevelsStoreInterface;
}

export const reducers = combineReducers<StoreState>({
  todos: todosReducer,
  auth: authReducer,
  socket: socketReducer,
  doExam: doExamReducer,
  application: applicationReducer,
  proctoring: proctoringReducer,
  system: systemReducer,
  applications_list: applicationsListReducer,
  positions: positionsReducer,
  periods: applicationPeriodsReducer,
  schoolLevels: schoolLevelsReducer,
});
